<template>
  <div class="container">
    <CollapsibleContent v-if="isOwnProfile">
      <template v-slot:collapsible-title>
          New Post
      </template>
      <template v-slot:collapsible-content>
        <div v-if="!isUploading" class="m-1 text-center">
          <label v-if="tryURL.trim().length <= 0" for="try-image" v-bind:style="`color: ${invertedHex};`" class="btn border m-2 p-3 h5"> Upload Photo</label><input type="file" id="try-image" @change="handleImageFile" hidden accept="image/*" />
          <label v-if="tryURL.trim().length <= 0" for="try-video" v-bind:style="`color: ${invertedHex};`" class="btn border m-2 p-3 h5"> Upload Video</label><input type="file" id="try-video" @change="handleVideoFile" hidden accept="video/*" />
          <button v-if="tryURL.trim().length > 0" type="button" class="btn border m-2 py-3 px-1 h5" @click="tryContentCardUpload()" v-bind:style="`color: ${invertedHex};`"> Fetch URL</button>

          <input type="text" class="col-12 text-center my-3 py-3" placeholder="Or paste a URL here. Try a YouTube, Spotify or Instagram public link...)" v-model="tryURL" />
        </div>
        <div class="text-center" v-else>
          <Spinner />
        </div>
      </template>
    </CollapsibleContent>

    <hr v-if="isOwnProfile" v-bind:style="`background-color: ${invertedHex};`" class="m-1" />

    <div id="scrollBox" @scroll="storiesScrolled" class="d-flex my-4" style="overflow-x: scroll;">
      <img role="button" @click="updateCurrentStory(Story)" v-for="Story of activeDocs" :id="Story.postUid" :key="Story.postUid" class="story-img mx-2" :src="Story.contentCard.previewImage || getStoryPreview(Story.contentCard)" v-bind:style="currentStory === Story ? `border: solid 3px; border-color: ${invertedHex}; background-color: ${invertedHex};` : `background-color: ${invertedHex};`" />
    </div>

    <hr v-bind:style="`background-color: ${invertedHex};`" class="m-1" />

    <div v-if="currentStory" class="p-3">
      <div v-if="isOwnProfile" class="my-3 d-flex justify-content-between">
        <h4><input v-model="editCurrentPost" type="checkbox" name="switch" id="editCurrentPostCheckbox" /> <label for="editCurrentPostCheckbox" role="button"> Edit <i class="fa fa-pencil mr-1"></i></label></h4>
        <button type="button" class="btn border m-2 py-3 px-1 h5" @click="() => { deleteContentCard(currentStory.postUid, currentStory.contentCard?.hostedFileName) }" v-bind:style="`color: ${invertedHex};`">  Delete</button>
      </div>

      <!-- TITLE -->
      <div v-if="editCurrentPost || currentStory.contentCard.title">
          <h1>
              <a v-if="!editCurrentPost" v-bind:style="`color: ${invertedHex};`" target="_blank" :href="currentStory.contentCard.sourceUrl" class="">{{ currentStory.contentCard.title }}</a>
              <EditableTextInput v-else :field="'title'" v-bind:style="`color: ${invertedHex};`" :value="currentStory.contentCard.title" :editMode="editCurrentPost" :postUid="currentStory.postUid" />
          </h1>
      </div>

      <!-- DESC -->
      <div class="mt-4 text-justify">
          <EditableTextInput :field="'description'" :value="currentStory.contentCard.description" :editMode="editCurrentPost" :postUid="currentStory.postUid" class="justify" />
      </div>

      <hr v-bind:style="`background-color: ${invertedHex};`" class="my-4" />

      <!-- IFRAME -->
      <iframe class="post-content" v-if="currentStory.contentCard.type === 'iframe'" :src="currentStory.contentCard.iFrameUrl"
        allowtransparency="true"
        frameborder="0"
        allow="accelerometer; encrypted-media; gyroscope; picture-in-picture"
        allowfullscreen
      />

      <!-- INSTA_POST -->
      <div id="injectedInsta" v-if="currentStory.contentCard.type === 'insta_post'">
      </div>

      <!-- IMAGE -->
      <img v-if="currentStory.contentCard.type === 'image'" class="w-100" :src="currentStory.contentCard.previewImage" style="cursor: pointer;" @click="handleImgClick" />

      <!-- VIDEO -->
      <video v-if="currentStory.contentCard.type === 'video'" class="w-100" :src="currentStory.contentCard.previewVideo" controls />
    </div>
    <div v-else class="my-5 py-5 text-center">
      <h1 v-bind:style="`color: ${invertedHex};`"> Upload your first Bio!</h1>
      <img class="fixed-bottom" src="@/assets/confused_travolta.gif" />
    </div>
  </div>
</template>

<script lang="ts">
/* eslint-disable @typescript-eslint/no-use-before-define */

import { defineComponent, reactive, toRefs, onMounted, onBeforeUnmount, nextTick } from 'vue'
import { auth, getTimelineCollection, storageRef } from '../firebase.config'
import { BaseContentCard } from '@/types/BaseContentCard'
import deleteContentCard from '@/services/deleteContentCard'
import { useToaster } from '@ajmey/toaster/toaster'
import { DOMEvent } from '@/types/DOMEvent'
import { randomInRange, getEmojis } from '@/utils/utils'
import { FetchUrlMeta } from '@/services/FetchUrlMeta'
import parseKnownUrl from '@/controllers/KnownUrlsParser'
import Spinner from '@/components/Spinner.vue'
import EditableTextInput from '@/components/EditableTextInput.vue'
import CollapsibleContent from '@/components/CollapsibleContent.vue'

const toaster = useToaster({ animation: 'appear', theme: 'default', dismiss: 3000 })

type Story = {
  postUid: string;
  contentCard: BaseContentCard;
}

export default defineComponent({
  props: {
    profileUid: String,
    invertedHex: String,
    isOwnProfile: Boolean
  },
  components: {
    Spinner,
    EditableTextInput,
    CollapsibleContent
  },
  async setup (props) {
    onMounted(() => {
      window.addEventListener('resize', handleResize)
      if (props.isOwnProfile) {
        timelineRef.orderBy('ts', 'desc').onSnapshot((refs) => {
          console.log('timelineRef onSnapshot', { refs })

          if (refs.docs.length === docsHandler.allDocs.length) {
            const allDocsIndex = docsHandler.allDocs.findIndex(({ id }) => id === docsHandler.currentStory.postUid)
            const activeDocsIndex = docsHandler.activeDocs.findIndex(({ postUid }) => postUid === docsHandler.currentStory.postUid)
            const freshStory = refs.docs.find(({ id }) => id === docsHandler.currentStory.postUid)
            if (!freshStory) { return }
            docsHandler.allDocs[allDocsIndex] = freshStory
            docsHandler.activeDocs[activeDocsIndex] = { postUid: freshStory.id, contentCard: freshStory.data() } as Story
            docsHandler.currentStory = docsHandler.activeDocs[activeDocsIndex]
          } else {
            docsHandler.allDocs = refs.docs
            docsHandler.activeDocs = docsHandler.allDocs.slice(0, pageSize).map(d => ({ postUid: d.id, contentCard: d.data() }) as Story)
            docsHandler.nextDocs = docsHandler.allDocs.slice(pageSize).map(d => ({ postUid: d.id, contentCard: d.data() }) as Story)
            docsHandler.prevDocs = []
            docsHandler.currentStory = docsHandler.activeDocs[0]
          }
        })
      }
    })
    onBeforeUnmount(() => {
      window.removeEventListener('resize', handleResize)
    })

    const storiesScrolled = (el: DOMEvent<HTMLElement>) => {
      const { target } = el
      const { scrollWidth, offsetWidth, scrollLeft } = target

      const onScrollStart = scrollLeft < storyWidth
      const onScrollEnd = scrollLeft > (scrollWidth - offsetWidth - storyWidth)

      if (onScrollStart && docsHandler?.prevDocs && docsHandler.prevDocs.length > 0) {
        const prevDoc = docsHandler.prevDocs.pop()
        if (prevDoc) {
          docsHandler.activeDocs.unshift(prevDoc)
        }
        if (docsHandler.activeDocs.length > pageSize * 2) {
          const lastActiveDoc = docsHandler.activeDocs.pop()
          if (lastActiveDoc) { docsHandler.nextDocs.unshift(lastActiveDoc) }
        }
        target.scrollLeft = target.scrollLeft * 4
        console.log('Scroll LEFT', docsHandler.activeDocs.length)
      } else if (onScrollEnd && docsHandler?.nextDocs && docsHandler.nextDocs.length > 0) {
        const nextDoc = docsHandler.nextDocs.shift()
        if (nextDoc) {
          docsHandler.activeDocs.push(nextDoc)
        }
        if (docsHandler.activeDocs.length > pageSize * 2) {
          const firstActiveDoc = docsHandler.activeDocs.shift()
          if (firstActiveDoc) { docsHandler.prevDocs.push(firstActiveDoc) }
        }
        console.log('Scroll RIGHT', docsHandler.activeDocs.length)
      }
    }

    const tryContentCardUpload = async (contentCard?: BaseContentCard): Promise<void> => {
      docsHandler.isUploading = true
      let newContentCard: Partial<BaseContentCard> = {}
      try {
        if (contentCard) {
          newContentCard = contentCard
        } else {
          newContentCard = await parseKnownUrl(docsHandler.tryURL.trim())
          if (newContentCard?.type) {
            newContentCard = {
              ...newContentCard,
              ...(await FetchUrlMeta(newContentCard.sourceUrl || '', newContentCard.type))
            }
          } else {
            newContentCard = {
              ...newContentCard,
              ...(await FetchUrlMeta(docsHandler.tryURL.trim()))
            }
          }
        }

        // Default to iframe content
        if (newContentCard.type === 'iframe' && !newContentCard?.iFrameUrl) {
          newContentCard.iFrameUrl = newContentCard.sourceUrl
        }
        // Default to emoji title/desc
        // if (!newContentCard?.title) {
        //   newContentCard.title = getEmojis(randomInRange(1, 3))
        // }
        // if (!newContentCard?.description) {
        //  newContentCard.description = getEmojis(randomInRange(1, 3))
        // }
        // Default to EMPTY title/desc
        if (!newContentCard?.title) {
          newContentCard.title = ''
        }
        if (!newContentCard?.description) {
          newContentCard.description = ''
        }

        const { currentUser } = auth
        const { uid } = currentUser || {}
        if (!uid) return
        const docRef = getTimelineCollection(uid)
        docRef.add(newContentCard).then(({ id: newPostUid }) => {
          docsHandler.tryURL = ''
          console.log('New link posted! ', { newPostUid })
          // router.push(`/u/${uid}/p/${newPostUid}`)
          docsHandler.isUploading = false
        })
      } catch ({ code, message }) {
        console.error('addContent Error', { code, message })
        docsHandler.isUploading = false
      }
    }

    const handleImageFile = async ({ target }: DOMEvent<HTMLInputElement>) => {
      docsHandler.isUploading = true
      try {
        const [file] = Array.from(target.files || [])
        const { name, size } = file
        if (size > 1 * 1024 * 1024) { // 1MB
          toaster.failure('Max. size: 1Mb ')
          target.value = ''
          docsHandler.isUploading = false
          return
        }

        const hostedFileName = `${new Date().getTime()}_${name}`
        const fileRef = storageRef.child(`${auth?.currentUser?.uid}/${hostedFileName}`)
        const { ref } = await fileRef.put(file)
        const downloadURL = await ref.getDownloadURL()
        target.value = ''
        await tryContentCardUpload({
          type: 'image',
          sourceUrl: downloadURL,
          previewImage: downloadURL,
          rawImages: [downloadURL],
          ts: new Date(),
          title: '',
          description: '',
          hostedFileName
        })
      } catch ({ code, message }) {
        console.error('handleImageFile Error', { code, message })
      }
      docsHandler.isUploading = false
    }

    const handleVideoFile = async ({ target }: DOMEvent<HTMLInputElement>) => {
      docsHandler.isUploading = true
      try {
        const [file] = Array.from(target.files || [])
        const { name, size } = file
        if (size > 15 * 1024 * 1024) { // 15MB
          toaster.failure('Max. size: 15Mb ')
          target.value = ''
          docsHandler.isUploading = false
          return
        }

        const hostedFileName = `${new Date().getTime()}_${name}`
        const fileRef = storageRef.child(`${auth?.currentUser?.uid}/${hostedFileName}`)
        const { ref } = await fileRef.put(file)
        const downloadURL = await ref.getDownloadURL()
        target.value = ''
        await tryContentCardUpload({
          type: 'video',
          sourceUrl: downloadURL,
          previewVideo: downloadURL,
          rawVideos: [downloadURL],
          ts: new Date(),
          title: '',
          description: '',
          hostedFileName
        })
      } catch ({ code, message }) {
        console.error('handleVideoFile Error', { code, message })
      }
      docsHandler.isUploading = false
    }

    const storyWidth = 75
    let windowWidth: number = window.innerWidth
    let pageSize = Math.ceil(windowWidth / storyWidth)
    const handleResize = async () => {
      windowWidth = window.innerWidth
      console.log({ windowWidth })
      const newPageSize = Math.ceil(windowWidth / storyWidth)
      pageSize = newPageSize
      /* if (newPageSize !== pageSize) {
        pageSize = newPageSize
        docsHandler.allDocs = (await timelineRef.orderBy('ts', 'desc').get()).docs
        docsHandler.activeDocs = docsHandler.allDocs.slice(0, pageSize).map(d => ({ postUid: d.id, contentCard: d.data() }) as Story)
        docsHandler.nextDocs = docsHandler.allDocs.slice(pageSize).map(d => ({ postUid: d.id, contentCard: d.data() }) as Story)
        docsHandler.prevDocs = []
        docsHandler.currentStory = activeDocs[0]
      } */
    }
    console.log({ pageSize })

    const timelineRef = getTimelineCollection(props.profileUid || '')
    const allDocs = (await timelineRef.orderBy('ts', 'desc').get()).docs
    const activeDocs = allDocs.slice(0, pageSize).map(d => ({ postUid: d.id, contentCard: d.data() }) as Story)
    const nextDocs = allDocs.slice(pageSize).map(d => ({ postUid: d.id, contentCard: d.data() }) as Story)
    const prevDocs: Story[] = []
    const currentStory: Story = activeDocs[0]

    const docsHandler = reactive({
      allDocs,
      activeDocs, // allDocs.slice(0, pageSize).map(d => ({ postUid: d.id, contentCard: d.data() }) as Story),
      nextDocs,
      prevDocs,
      currentStory,
      tryURL: '',
      tryFile: null,
      isUploading: false,
      editCurrentPost: false
    })

    const getStoryPreview = (contentCard: BaseContentCard) => {
      if (contentCard.iFrameUrl) {
        if (contentCard.iFrameUrl.includes('youtube.com') || contentCard.iFrameUrl.includes('youtu.be')) {
          return '/img/youtube_logo.png'
        }
        if (contentCard.iFrameUrl.includes('spotify.com')) {
          return '/img/spotify_logo.png'
        }
        if (contentCard.iFrameUrl.includes('instagram.com')) {
          return '/img/insta_logo.png'
        }
        return '/img/iframe.png'
      }
      if (contentCard.previewVideo) { return '/img/video.png' }
      return '/img/media_logo.png'
    }

    const updateCurrentStory = (story: Story) => {
      docsHandler.currentStory = story
      nextTick(() => {
        const p = document.getElementById('injectedInsta')
        if (p) {
          p.innerHTML = `<blockquote class="instagram-media post-content" data-instgrm-captioned
            data-instgrm-permalink="${story.contentCard.iFrameUrl}"
            data-instgrm-version="13"
          ></blockquote>`
        }
        window.instgrm.Embeds.process()
      })
    }

    return {
      ...toRefs(docsHandler),
      storiesScrolled,
      deleteContentCard,
      tryContentCardUpload,
      handleImageFile,
      handleVideoFile,
      getStoryPreview,
      updateCurrentStory
    }
  }
})
</script>

<style>
img.story-img {
  height: 75px;
  width: 75px;
  border-radius: 50%;
  border-width: 5px;
  object-fit: cover;
}
.post-content {
  width: 100%;
  max-width: 1200px;
  min-height: 500px;
  object-fit: contain;
}
</style>
