<template>
  <div class="container-fluid m-0 p-0" v-bind:style="`background-color: ${updateColor || bgColor}; color: ${invertedHex}`">
    <div class="p-2 p-md-4 text-right">
      <button v-if="isOwnProfile" type="button" class="btn btn-light border m-1" @click="editColorMode = !editColorMode">{{editColorMode ? 'Hide Picker' : 'Change Color'}} <i class="fa fa-paint-brush"></i></button>
      <button type="button" class="btn btn-light border m-1" @click="showShareProfile = !showShareProfile">{{showShareProfile ? 'Hide Share' : 'Share Bio'}} <i class="fa fa-external-link"></i></button>
      <button v-if="isLoggedIn" type="button" class="btn btn-light border m-1" @click="signOut">Close Session <i class="fa fa-sign-out"></i></button>
      <button v-if="!isLoggedIn" type="button" class="btn btn-light border m-1" @click="signIn">Sign In <i class="fa fa-sign-in"></i></button>
    </div>

    <div v-show="showShareProfile" id="share-box" class="row justify-content-center p-3">
      <div class="m-2" id="qr-box"></div>
      <div class="m-2" style="overflow-x: scroll;">
        <h3 class="mb-4">Share this Bio!</h3>
        <p><strong> Share Link</strong></p>
        <a style="text-decoration: none;" target="_blank" :href="shareLinks.regular">{{shareLinks.regular}}</a>
      </div>
    </div>

    <div v-show="isOwnProfile && editColorMode" class="row justify-content-center p-4">
      <div id="color-picker"></div>
    </div>

    <div v-if="isOwnProfile" class="m-3">
       <h4><input type="checkbox" name="switch" v-model="editMode" id="editModeCheckbox" /> <label for="editModeCheckbox" role="button"> Edit profile <i class="fa fa-pencil mr-1"></i></label></h4>
    </div>

    <EditableImgInput :field="'coverImg'" :value="coverImg" :editMode="editMode" class="cover-img" />
    <EditableImgInput :field="'photoURL'" :value="photoURL" :editMode="editMode" v-bind:style="editMode ? '':'margin-top: -55px;'" class="profile-img" :border-color="invertedHex"/>

    <h1><EditableTextInput :field="'displayName'" :value="displayName" :editMode="editMode" class="text-center"/></h1>
    <EditableTextInput :field="'shortBio'" :value="shortBio" :editMode="editMode" class="text-center" />

    <div class="text-center mt-4">
      <h2 v-if="!editMode">
        <span v-for="({ icon, link, enabled}, key) in socialLinksToShow" :key="key">
          <a v-if="enabled" v-bind:style="`color: ${invertedHex};`" target="_blank" :href="link" class="m-2">
            <i :class="icon"></i>
          </a>
        </span>
      </h2>
      <h2 v-if="editMode">
        <a style="cursor: pointer;" class="m-2" v-bind:style="`color: ${enabled ? invertedHex : 'darkgrey'};`" @click="handleSocialLinkClick(key, link)" v-for="({ icon, link, enabled}, key) in socialLinksToShow" :key="key">
          <i :class="icon"></i>
        </a>
      </h2>
      <input v-if="editMode && editSocialLinkMode" type="text" class="text-center m-1 p-1 col-6" v-model="trySocialLink" @blur="({ target: { value } }) => updateSocialLink(socialLinkInputFor, value, socialLinksToShow[socialLinkInputFor].link)" :placeholder="`Paste ${socialLinkInputFor} URL`" />
    </div>

    <div class="container">
      <CollapsibleContent v-if="longBio.length > 0 || editMode">
        <template v-slot:collapsible-title>
            Bio
        </template>
        <template v-slot:collapsible-content>
          <EditableTextInput :field="'longBio'" :value="longBio" :editMode="editMode" class="mb-4" v-bind:class="longBio.length < 140 ? 'h4 text-center' : 'text-justify'" />
        </template>
      </CollapsibleContent>
    </div>

    <hr v-bind:style="`background-color: ${invertedHex};`" class="m-1" />
    <!-- hr v-if="longBio.trim().length > 0 || editMode" v-bind:style="`background-color: ${invertedHex};`" class="m-1" /-->

    <Suspense>
      <template #default>
        <ProfileTimeline :profileUid="profileUid" :invertedHex="invertedHex" :isOwnProfile="isOwnProfile" />
      </template>
      <template #fallback>
        <Spinner />
      </template>
    </Suspense>
  </div>
</template>

<script lang="ts">
/* eslint-disable @typescript-eslint/no-use-before-define */
/* eslint-disable @typescript-eslint/ban-ts-ignore */
/* eslint-disable no-undef */

import { defineComponent, toRefs, reactive, computed, onMounted } from 'vue'
import { auth, profilesCollection } from '@/firebase.config'
import { UserProfile } from '@/types/UserProfile'
import updateProfileValue from '@/services/updateProfileValue'
import router from '@/router'
import { invertHex } from '@/utils/utils'
import EditableTextInput from '@/components/EditableTextInput.vue'
import EditableImgInput from '@/components/EditableImgInput.vue'
import CollapsibleContent from '@/components/CollapsibleContent.vue'
import ProfileTimeline from '@/components/ProfileTimeline.vue'
import Spinner from '@/components/Spinner.vue'
import Picker from 'vanilla-picker'

const updateSocialLink = (site: string, link: string, prevLink: string) => {
  updateProfileValue(`socialLinks.${site}.link`, link, prevLink)
}

export default defineComponent({
  props: {
    profileUid: String
  },
  components: {
    EditableTextInput,
    EditableImgInput,
    CollapsibleContent,
    ProfileTimeline,
    Spinner
  },
  async setup (props) {
    onMounted(async () => {
      const parent = document.getElementById('color-picker')
      if (parent) { parent.innerHTML = '' }
      // eslint-disable-next-line no-undef
      const picker = new Picker({ parent, alpha: false, popup: false, color: reactiveFields.updateColor || reactiveFields.bgColor }) // https://vanilla-picker.js.org/gen/Picker.html#Picker__anchor

      picker.onChange = ({ hex: hexChange }: { hex: string}) => {
        // console.log({ hexChange })
        reactiveFields.updateColor = hexChange.slice(0, 7)
      }
      picker.onDone = () => {
        // console.log({ hexDone })
        updateProfileValue('bgColor', reactiveFields.updateColor, reactiveFields.bgColor)
        reactiveFields.editColorMode = false
      }
      /* picker.onClose = () => {
        // console.log({ hexClosed })
        reactiveFields.updateColor = ''
      } */
      picker.onOpen = ({ hex: hexOpened }: { hex: string}) => {
        reactiveFields.updateColor = hexOpened.slice(0, 7)
      }

      // @ts-ignore: no-undef
      const newQR = new VanillaQR({
        url: shareLinks.regular,
        // colorLight: invertedHex.value || 'white',
        // colorDark: reactiveFields?.updateColor || reactiveFields?.bgColor || 'black',
        noBorder: true
      })
      const qrBox = document.getElementById('qr-box')
      if (!qrBox) return
      qrBox.innerHTML = ''
      qrBox.appendChild(newQR.domElement)

      // Keep updating user profile template for own user profile
      if (isOwnProfile) {
        userRef.onSnapshot((userDoc) => {
          const { displayName, photoURL, coverImg, shortBio, longBio, bgColor, socialLinks } = userDoc.data() as UserProfile
          Object.assign(reactiveFields, { displayName, photoURL, coverImg, shortBio, longBio, bgColor, socialLinks })
          console.log('Profile onSnapshot', { displayName, photoURL, coverImg, shortBio, longBio, bgColor, socialLinks })
        })
      }
    })

    const isLoggedIn = auth && auth?.currentUser !== null && auth?.currentUser !== undefined
    const isOwnProfile = auth?.currentUser?.uid === props?.profileUid
    const userRef = profilesCollection.doc(props.profileUid)
    const userDoc = await userRef.get()
    const { displayName, photoURL, coverImg, shortBio, longBio, bgColor, socialLinks } = userDoc.data() as UserProfile

    const reactiveFields = reactive({
      editMode: false,
      editColorMode: false,
      editSocialLinkMode: false,
      updateColor: bgColor,
      showShareProfile: false,
      socialLinkInputFor: '',
      trySocialLink: '',
      displayName,
      photoURL,
      coverImg,
      shortBio,
      longBio,
      bgColor,
      socialLinks
    })
    const invertedHex = computed(() => invertHex(reactiveFields.updateColor || reactiveFields.bgColor))

    const shareLinks = {
      // regular: `/u/${props.profileUid}`
      // insta: `/user?u=${props.profileUid}`
      regular: `https://instab.io/user?u=${props.profileUid}`
    }

    const socialLinksToShow = computed(() => {
      const filled: Record<string, unknown> = {}
      Object.keys(reactiveFields.socialLinks).forEach(key => {
        const { link = '', icon } = reactiveFields?.socialLinks[key]
        const enabled = link?.length > 0
        filled[key] = {
          link,
          icon,
          enabled
        }
      })
      return filled
    })

    const handleSocialLinkClick = (site: string, currentLink = '') => {
      reactiveFields.editSocialLinkMode = !reactiveFields.editSocialLinkMode
      if (!reactiveFields.editSocialLinkMode) {
        reactiveFields.socialLinkInputFor = ''
        reactiveFields.trySocialLink = ''
        return
      }
      reactiveFields.socialLinkInputFor = site
      reactiveFields.trySocialLink = currentLink
    }

    const signOut = () => auth.signOut()
    const signIn = () => router.replace('/')

    return { ...toRefs(reactiveFields), invertedHex, signIn, signOut, isLoggedIn, isOwnProfile, shareLinks, socialLinksToShow, handleSocialLinkClick, updateSocialLink }
  }
})
</script>

<style>
#qr-box canvas {
  width: 110px;
  height: 110px;
}

.cover-img img {
  height: 250px;
  object-fit: cover;
}

.profile-img img {
  height: 150px;
  width: 150px;
  border-radius: 50%;
  object-fit: cover;
}
</style>
