<template>
    <p style="margin: 5px;" :contenteditable="editMode" v-bind:style="`border: ${editMode ? 'dotted; black;' : 'none;'} border-width: ${editMode ? '1px;' : 'none;'}`" @blur="({ target: { textContent }}) => { updateFn(textContent) }">{{value}}</p>
</template>

<script lang="ts">
import { toRefs, defineComponent } from 'vue'
import updateProfileValue from '@/services/updateProfileValue'
import updateCardValue from '@/services/updateCardValue'

export default defineComponent({
  props: {
    field: {
      type: String,
      default: ''
    },
    value: String,
    editMode: Boolean,
    postUid: String
  },
  setup (props) {
    const updateFn = (textContent: string) => props.postUid ? updateCardValue(props.postUid, props.field, textContent, props.value) : updateProfileValue(props.field, textContent, props.value)
    return {
      ...toRefs(props),
      updateProfileValue,
      updateFn
    }
  }
})
</script>
